import React from "react";
import styled from "styled-components";
import { useForm } from "react-hook-form";
const StyledContainer = styled.div`
	padding: 15px 10px;
	.input-group {
		margin: 20px;
		label {
			margin-right: 20px;
			font-size: 1.4rem;
			font-weight: 400;
		}
		input {
			border: solid 1px #e2e2e2;
			border-radius: 10px;
			font-size: 1.2rem;
			color: chocolate;
			padding: 10px;
		}
	}
	button {
		background-color: blue;
		padding: 15px 10px;
		width: 100%;
		font-size: 15px;
		color: white;
		border-radius: 15px;
	}
	small {
		color: red;
	}
`;

export default function InputTest() {
	const {
		register, 
		handleSubmit,
		formState: { isSubmitting, isDirty, errors },
	}  = useForm();
	return (
		<StyledContainer>
			<h1>input 요소 테스트</h1>
			<form onSubmit={handleSubmit((data) => alert(JSON.stringify(data)))}>
				<div className="input-group">
					<label>이름</label>
					<input type="text" name="name" {...register('name', {
						required: "이름은 필수 입력입니다.",
						minLength: {
							value: 2,
							message: "2자리 이상 입력해주세요.",
						},
					})}/>
				</div>
				{errors.name && <small>{errors.name.message}</small>}

				<div className="input-group">
					<label>나이</label>
					<input type="tel" name="age" {...register('age', {
						required: "나이는 필수 입력입니다.",
						onChange: (e) => {	
							// 숫자만 입력 가능
							const value = e.target.value;
							e.target.value = Number(value) ? value : value.slice(0, -1)
						},
						validate:  {
							checkAge: (value) => value < 19 ? "만 19세 이상만 가입가능합니다." : true
						}
					})}/>
				</div>
				{errors.age && <small>{errors.age.message}</small>}

				<div className="input-group">
					<label>종류</label>
					<input type="text"   {...register('kind', {    
						required: '종류를 입력해주세요.',
						// required: true,
						// boolean값도 가능하지만 문자열 값을 주면, input의 value가 없을 때 해당 문자열이 errors 객체로 반환되어 에러 메세지로 표시할 수 있다.
						minLength: { // value의 최소 길이
							value: 3,
							message: '3글자 이상 입력해주세요.', // 에러 메세지
						},
						pattern: { // input의 정규식 패턴
							value: /^[A-za-z0-9가-힣]{3,10}$/,
							message: '가능한 문자: 영문 대소문자, 글자 단위 한글, 숫자', // 에러 메세지
						},
					})} />
				</div>
				{errors.kind && <small>{errors.kind.message}</small>}

				<div className="input-group">
					<input {...register("Developer", { required: true })} type="radio" value="Y" />
					<input {...register("Developer", { required: true })} type="radio" value="N" />
				</div>


				<button type="submit" disabled={isSubmitting}> 입력완료 </button>
			</form>
		</StyledContainer>
	);
}
