import React, { useState } from "react";
import { useAlert, useBottomSheet, useCustomModal } from "@hooks/useModal";
import styled from 'styled-components'
import Input from "@components/Input";
import { format } from "date-fns";
import { useInput } from "../../hooks/useInput";
import { PageLayout } from "@chac/common/style/sytles";
const StyledContainer = styled(PageLayout)`
	.title {
		text-align: left;
		font-size: 1.4rem;
		font-weight: 400;
	}
	.box {
		border: solid 1px #e2e2e2;
		padding: 20px 15px;
		margin: 15px auto;
	}
	.input-box {
		text-align: left;
		padding: 10px;
    	margin: 15px;
		label {
			font-weight: 400;
			font-size: 1.4rem;
			margin-right: 10px;
		}
		input {
			color: blue;
			background: white;
			padding: 5px 10px;
			border-bottom: solid 1px;
			border-radius: 15px;
		}
		input:read-only{
			color: gray;
			background: #d2d2d2;
		}
	}
	button {
		background: blue;
		color: white;
		padding: 10px;
		border-radius: 20px;
	}
`
/**
 * 
 * @returns 
 */
export default function ModalTest() {
	// 모달테스트 영역

	// 0. 모달용 함수
	const [showAlert] = useAlert();
	const [showCustomModal] = useCustomModal();	
	// 1. input
	const [{modalTitle, modalContent, name, age}, setValue, onChange, reset] = useInput({
		modalTitle: 'Title 문구',
		modalContent: 'Content 문구',
		name: '이강훈',
		age: '33'
	})
	const [number, setNumber] = useState(0);


	const btnModalClick = () => {
		showAlert({
			title: modalTitle,
			content: modalContent,
			closeHandler: function (result) {
				console.log(result)
				console.log(modalTitle + " 팝업을 종료합니다");
			},
		});
	};
	// 2. datePicker
	const [date, setDate] = useState(format(new Date(), "yyyy/MM/dd"));

	// 3. Custom Modal
	const btnCustomModalClick = () => {
		// path: "modals/" 폴더 내 파일명,
		// closeHandler: 모달종료 시, 액션
		// 전달할 데이터는 원하는 key값에 담아 전달 가능  ex) props
		showCustomModal({
			path: "CustomSampleModal",
			props: {
				name,
				age,
				number
			},
			closeHandler: function (result) {
				if (result == 'cancel') {
					return;
				}
				setNumber(result.number);
				console.log('받은 데이터>>>>>>>', result)
				console.log(modalTitle + " 팝업을 종료합니다");
			},
		});
	};
	return (
		<StyledContainer>
			<h3> 모달/컴포넌트 테스트 </h3>
			<div className="box">
				<h2 className="title">1. input 값 받아서 Alert에 전달</h2>
				<div className="input-box">
					<label>제목</label>
					<input type="text" name="modalTitle" value={modalTitle} onChange={onChange} />
				</div>
				<div className="input-box">
					<label>내용</label>

					<input type="text" name="modalContent" value={modalContent} onChange={onChange} />
				</div>
				<button onClick={btnModalClick}> 모달띄우기 </button>
			</div>

			<div className="box">
				<h2 className="title">2. datePicker 값으로 모달 띄우기 (미사용)</h2>
				<Input
					type="date"
					value={date}
					setValue={setDate}
					onChange={(val) =>
						showAlert({ title: "선택한 날짜", content: val })
					}
				/>
			</div>
			<div className="box">
				<h2 className="title">3. 커스텀 모달 사용</h2>
				<h3 className="sub-title">보내는 데이터</h3>
				<div className="input-box">
					<label>이름</label>
					<input type="text" value={name} name="name" onChange={onChange} />
				</div>
				<div className="input-box">
					<label>나이</label>
					<input type="text" value={age} name="age" onChange={onChange} />
				</div>
				<h3 className="sub-title">받는 데이터</h3>
				<div className="input-box">
					<label>번호</label>
					<input type="text" value={number} name="number" disabled/>
				</div>
				<button onClick={btnCustomModalClick}>
					지정된 파일 모달 띄우기
				</button>
			</div>
		</StyledContainer>
	);
}
