import { createSlice } from "@reduxjs/toolkit"

// 아직 어떤게 필요할지 명확하지 않기에, 동물등록 화면의 정보 기준으로 임시 세팅함
const initialState = {
	type: '',					// 개/고양이 타입
	image: null,			// 사진
	kind: '',				// 견/묘종
	color: '',				// 털색
	name: '',					// 이름
	weight: 0,					// 무게
	gender: '',					// 성별
	isDeSexing: false,			// 중성화
	birth: '',					// 생일
	adoptionDate: '',			// 입양일
	isRegisterNo: false,		// 동물등록번호 여부
	registerNo: '',				// 동물등록번호 정보 { 반려인 이름 / 등록번호 }
	masterName: '',
	isPetInsurance: false,		// 펫보험 가입여부
}

export const profileSlice = createSlice({
	name: 'profile',
	initialState,
	reducers: {
		setProfile: function (state, action) {
			return (action.payload);
		}
	}
})

export const {setProfile} = profileSlice.actions;
export default profileSlice.reducer;
